import { postResoluciones, getResoluciones } from "../api/resoluciones";
import Formulario from "../components/resoluciones/Formulario";

// const thisYear = (new Date()).getFullYear(); 
// let years = []
// for (let i = thisYear; i >= 2016; i--)
//     years.push(i);

const years = [2022, 2021, 2020, 2019, 2018, 2017, 2016];

export default {
  years,
  components: {
    Formulario,
  },
  data() {
    return {
      yearSelected: 2022,
      datos: null,
    };
  },
  mounted() {
    this.cargarDatos();
  },
  computed: {
    datosSelected() {
      if (!this.datos) return null;
      return this.datos.find((x) => x.year == this.yearSelected);
    },
  },
  methods: {
    mensajeError(texto) {
      console.error(texto);
      return this.$swal({
        title: "Error",
        html: `Ocurrio un error inesperado al realizar la operación.<br/>
        Por favor, si el error persiste contacte al administrador del sistema.`,
        icon: "error",
        allowOutsideClick: false,
      });
    },
    mensajeExito() {
      return this.$swal({
        title: "Exito",
        text: "La operación se realizo correctamente.",
        icon: "success",
        allowOutsideClick: false,
      });
    },
    cargarDatos() {
      let loader = this.$loading.show();

      getResoluciones()
        .then((data) => {
          this.datos = data.resoluciones;
        })
        .catch((error) => {
          this.mensajeError(error);
        })
        .finally(() => loader.hide());
    },
    async actualizar(data) {
      const accessToken = await this.$auth.getTokenSilently();

      let loader = this.$loading.show();
      postResoluciones(accessToken, data)
        .then(() => {
          this.mensajeExito();
        })
        .catch((error) => {
          this.mensajeError(error);
        })
        .finally(() => loader.hide());
    },
  },
};
