const urlDashboard = process.env.VUE_APP_URL_DASHBOARD;

export default {
  urlDashboard,
  name: "NavBar",
  methods: {
    login() {
      this.$auth.loginWithRedirect();
    },
    logout() {
      this.$auth.logout({
        returnTo: window.location.origin + process.env.BASE_URL.slice(0, -1)
      });
      this.$router.push({ path: "/" });
    }
  }
};
